import { determineMainDrawerLevel } from './routing-key';

describe('determineMainDrawerLevel', () => {
  it('should return level 1 if activeRoutingKey is mainmenu', () => {
    const level = determineMainDrawerLevel('mainmenu');

    expect(level).toEqual(1);
  });

  it('should return level 2 if activeRoutingKey is models', () => {
    const level = determineMainDrawerLevel('models');

    expect(level).toEqual(2);
  });

  it('should return level 2 if activeRoutingKey is vehicle_purchase', () => {
    const level = determineMainDrawerLevel('vehicle_purchase');

    expect(level).toEqual(2);
  });

  it('should return level 3 if activeRoutingKey is models/mainmenu.911', () => {
    const level = determineMainDrawerLevel('models/mainmenu.911');

    expect(level).toEqual(3);
  });
});
